.nav-bar-container {
    position: relative;
    margin-bottom: 10px;
}

.nav-bar-subcontainer {
    position: fixed;
    width: 100%;
    justify-content: space-between;
    height: 50px;
    padding: 5px;
    background-color: $clr4;
    z-index: 3;
}

.logo {
    font-family: "amaticbold";
    font-size: rem(32px);
    letter-spacing: rem(1px);

    &:hover {
        color: #959191;
    }
}

.nav-bar-menu *:not(:last-child) {
    margin-inline-end: rem(40px);
}

.menu-btn {
    display: none;
}

@include for-mobile-wide-layout {

    .nav-bar-subcontainer {
        position: absolute;
        right: 0;
        padding: inherit;
        align-items: center;
    }

    .nav-bar-menu {
        display: none;
    }

    .menu-btn {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        // align-self: flex-end;
        width: 30px;
        height: 30px;
        transition: all 0.5s ease-in-out;
        border: 3px solid $clr2;
        border-radius: 4px;
        margin-top: 5px;
    }

    .menu-btn__burger {
        width: 18.75px;
        height: 3px;
        background: $clr2;
        border-radius: 4px;
        box-shadow: 0 2px 5px rgba(225, 101, 47, 0.2);
        transition: all 0.3s ease-in-out;
    }

    .menu-btn__burger::before,
    .menu-btn__burger::after {
        content: "";
        position: absolute;
        width: 18.75px;
        height: 3px;
        background: $clr2;
        border-radius: 4px;
        box-shadow: 0 2px 5px rgba(225, 101, 47, 0.2);
        transition: all 0.3s ease-in-out;
    }

    .menu-btn__burger::before {
        transform: translateY(-7px);
    }

    .menu-btn__burger::after {
        transform: translateY(7px);
    }

    .menu-btn.open .menu-btn__burger {
        background: transparent;
        box-shadow: none;
    }
    
    .menu-btn.open .menu-btn__burger::before {
        transform: rotate(45deg);
    }
    
    .menu-btn.open .menu-btn__burger::after {
        transform: rotate(-45deg);
    }

    .mobile-menu {
        display: flex;
        position: absolute;
        flex-direction: column;
        height: 232px;
        top: 50px;
        left: 0;
        width: 100%;
        padding: 0 15px;
        background-color: $clr4;
        z-index: 1000;
    }

    .nav-bar-menu *:not(:last-child) {
        margin-inline-end: unset;
    }

    .nav-bar-menu div {
        padding: 10px 0px;
        border-bottom: 1px solid $clr2;
    }
    
}
