.empty-page-img {
    margin-inline-end: 20px;
    width: 50%;
}

.empty-page-txt {
    margin-bottom: 20px;
}

@include for-mobile-wide-layout {
    .empty-page-container {
        flex-direction: column;
    }

    .empty-page-img {
        width: 100%;
        margin-inline-end: unset;
        margin-bottom: 20px;
    }

    .empty-page-txt {
        text-align: center;
    }
}
