.home-container {
    margin-top: 50px;
}

.slideshow-container {
    width: 80%;
    margin-inline-end: 10px;
}

.action-container {
    max-width: 25%;
    text-align: center;
}

.gallery-btn {
    display: flex;
    align-self: center;
}

@include for-mobile-wide-layout {
    .home-container {
        flex-direction: column;
    }

    .slideshow-container {
        width: 100%;
        height: 240px;
        margin-inline-end: unset;
        margin-bottom: 10px;
    }

    .action-container {
        max-width: 100%;
    }
}