.gallery-preview-container {
    position: relative;
    width: 30%;
    text-align: center;
    margin-bottom: 10px;
}

.gallery-preview-container:not(:last-child) {
    margin-inline-end: 10px;
}

.gallery-preview-container:nth-child(3n) {
    margin-inline-end: 0px;
}

.gallery-preview-container img {
    position: relative;
    width: 100%;
    border-radius: 4px;
}

.gallery-preview-container-screen {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.74);
    color: white;
    opacity: 0;
    padding: 0 10px;
    transition: opacity 0.5s;

    &:hover {
        opacity: 1;
    }
}

@include for-mobile-wide-layout {
    .gallery-preview-container-screen {
        display: none;
    }
}