.gallery-item-container {
    margin-top: 50px;
}

.gallery-item-subcontainer {
    overflow-x: hidden;
}

.gallery-item-container img {
    width: 100%;
    border-radius: 4px;
}

.gallery-item-buttons {
    margin-bottom: 22px;
}

.gallery-item-buttons :not(:last-child) {
    margin-inline-end: 10px;
}

.item-field:not(:last-child) {
    margin-bottom: 10px;
}