.cmp-animation-enter {
    opacity: 0;
}

.cmp-animation-enter-active {
    opacity: 1;
    transition: opacity 700ms;
}
.cmp-animation-exit {
    opacity: 1;
}
.cmp-animation-exit-active {
    opacity: 0;
    transition: opacity 1000ms;
}

.gallery-item-animation-enter {
    opacity: 0;
    transform: translateX(100%);
}

.gallery-item-animation-enter-active {
    opacity: 1;
    transform: translateX(0%);
    transition: opacity 1000ms, transform 1200ms;
}
.gallery-item-animation-exit {
    opacity: 1;
    transform: translateX(0%);
}
.gallery-item-animation-exit-active {
    opacity: 0;
    transform: translateX(-100%);
    transition: opacity 1000ms, transform 1500ms;
}