@keyframes cf4FadeInOut {
    0% {
        opacity: 1;
    }
    17% {
        opacity: 1;
    }
    25% {
        opacity: 0;
    }
    92% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.slideshow-inner-container {
    height: 100%;
    width: 100%;
    position: relative;
}

.slides-container {
    align-items: center;
    position: relative;
    width: 100%;
    height: 100%;
}

.slides-container img {
    position: absolute;
    animation-name: cf4FadeInOut;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 8s;
    -webkit-transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    -o-transition: opacity 1s ease-in-out;
    transition: opacity 1s ease-in-out;
    width: 100%;
    border-radius: 4px;
}

.slideshow-inner-container img:nth-of-type(1) {
    animation-delay: 6s;
}
.slideshow-inner-container img:nth-of-type(2) {
    animation-delay: 4s;
}
.slideshow-inner-container img:nth-of-type(3) {
    animation-delay: 2s;
}
.slideshow-inner-container img:nth-of-type(4) {
    animation-delay: 0s;
}

@include for-mobile-wide-layout {
    .slides-container {
        align-items: flex-start;
    }
}